@layer reset, typography, base, components, utilities;

@mixin screen($screen) {
  @media (width < #{$screen}) {
    @content;
  }
}

@layer reset {
  .leaflet-control {
    display: none !important;
  }
  .App {
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
  }

  main {
    display: block;
    flex-grow: 1;
    margin-top: 11.75rem;
  }

  header,
  footer {
    flex-shrink: 0;
  }

  a,
  button {
    cursor: pointer;
  }

  * {
    padding: 0px;
    margin: 0px;
    border: none;
  }

  *,
  *::before,
  *::after {
    box-sizing: border-box;
  }

  /* Links */

  a,
  a:link,
  a:visited {
    text-decoration: none;
    color: inherit;
    display: block;
  }

  a:hover {
    text-decoration: none;
  }

  /* Common */

  aside,
  nav,
  footer,
  header,
  section,
  main {
    display: block;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    font-size: inherit;
    font-weight: inherit;
  }

  ul,
  ul li {
    list-style: none;
  }

  img {
    vertical-align: top;
  }

  img,
  svg {
    max-width: 100%;
    height: auto;
  }

  address {
    font-style: normal;
  }

  /* Form */

  input,
  textarea,
  button,
  select {
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
  }

  input::-ms-clear {
    display: none;
  }

  button,
  input[type="submit"] {
    display: inline-block;
    box-shadow: none;
    background-color: transparent;
    background: none;
    cursor: pointer;
  }

  input:focus,
  input:active,
  button:focus,
  button:active {
    outline: none;
  }

  button::-moz-focus-inner {
    padding: 0;
    border: 0;
  }

  label {
    cursor: pointer;
  }

  legend {
    display: block;
  }

  * {
    -webkit-appearance: none;
    appearance: none;
  }


  /* 
  404
  */
  .error-404 {
    margin-top: 20vh;
    display: grid;
    align-items: center;
    justify-items: center;
    height: 100%;

    a {
      color: #33f;
    }
  }
}

@layer typography {

  .fw1 {
    font-weight: 100;
  }

  .fw2 {
    font-weight: 200;
  }

  .fw3 {
    font-weight: 300;
  }

  .fw4 {
    font-weight: 400;
  }

  .fw5 {
    font-weight: 500;
  }

  .fw6 {
    font-weight: 600;
  }

  .fw7 {
    font-weight: 700;
  }

  .fw8 {
    font-weight: 800;
  }

  .fw9 {
    font-weight: 900;
  }

  body * {
    font-family: "Rubik", sans-serif;
  }

  $fontSizes: (
    (100, 64, 100),
    (96, 48, 96),
    (60, 32, 60),
    (48, 24, 48),
    (40, 22, 40),
    (32, 20, 32),
    (30, 20, 30),
    (28, 18, 28),
    (24, 16, 24),
    (20, 14, 20),
    (18, 14, 18),
    (16, 14, 16),
    (14, 12, 14),
    (12, 12, 12)
  );

  $maxWidth: 1920 !default;


  @mixin adaptiv-font($fontSizes) {
    @each $size in $fontSizes {
      $fontSizeKey: nth($size, 1);
      $mobSize: nth($size, 2);
      $pcSize: nth($size, 3);

      $addSize: $pcSize - $mobSize;
      $adjustedMaxWidth: $maxWidth - 375;

      .fs-#{$fontSizeKey} {
        font-size: min(calc(#{$mobSize}px + #{$addSize} * ((100vw - 375px) / #{$adjustedMaxWidth})), #{$pcSize}px);
      }
    }
  }

  @include adaptiv-font($fontSizes);


