@layer base {
  :root {

    --bg: #fff;
    --black: #000;
    --white: #fff;
    --blue: #4880ff;
    --red: #FF4848;

    --display: 1720px;
    --display-full: 1920px;
    --padding-inline: 2.5rem;
  
    @media(width < 768px) {
      --padding-inline: 1.25rem;
    }
  }

  .__burger_icon {
    --color: var(--black);
    display: block;
    font-size: 1.5em;
    width: 1em;
    height: 1em;
    overflow: hidden;
    position: relative;
    z-index: 9999;

    span {
      display: block;
      position: absolute;
      width: 1em;
      height: 2px;
      left: 0;
      background-color: var(--color);
      transition: all .2s ease;
    }

    span:nth-child(1) {
      position: absolute;
      top: 0.2em;
    }

    span:nth-child(2) {
      position: absolute;
      top: 0.5em;
    }

    span:nth-child(3) {
      position: absolute;
      top: 0.8em;
    }

    &.open {
      span:nth-child(1) {
        position: absolute;
        top: 0.45em;
        transform: rotate(45deg);
      }

      span:nth-child(2) {
        position: absolute;
        transform: translate(2em);
      }

      span:nth-child(3) {
        position: absolute;
        top: 0.45em;
        transform: rotate(-45deg);
      }
    }
  }

  html,
  body {
    width: 100%;
    scroll-behavior: smooth;
    color: var(--black);
    background-color: var(--bg);
    position: relative;
    overflow-x: hidden;
  }

  svg {
    flex-shrink: 0;
  }

  section {
    margin-top: 2.5rem;

    @include screen(768px) {
      margin-top: 1.25rem;
    }
  }

  .retreat {
    margin-top: 2.5em;

    @include screen(768px) {
      margin-top: 1.25em;
    }

    &--double {
      margin-top: 5em;

      @include screen(768px) {
        margin-top: 2.5em;
      }
    }

    &--none {
      margin-top: unset;
    }
  }

  .wrapper {
    width: min(var(--display), calc(100% - var(--padding-inline) * 2));
    margin-inline: auto;

    &--full {
      width: min(var(--display-full), 100%);
    }
  }


  header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
    margin-top: 3rem;
    transition: all 0.3s ease;
    .header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: var(--bg);
      border-radius: 0.625rem;
      padding: calc(var(--padding-inline) / 2) var(--padding-inline);
      &__mobile {
        display: none;
        @include screen(796px) {
          display: flex;
          justify-content: flex-end;
          align-items: center;
        }
      }
      &__nav {
        @include screen(796px) {
          position: fixed;
          top: 0;
          bottom: 0;
          right: -100vw;
          height: 100vh;
          width: 100vw;
          z-index: 999;
          background: var(--bg);
          display: flex;
          gap: 2.5rem;
          transition: all 0.3s ease;
          &.open {
            right: 0;
          }
          ul {
            margin-top: 4rem;
            flex-grow: 1;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding-bottom: calc(var(--padding-inline) *2);
            li {
              &:last-of-type {
                order: -5;
              }
              &:nth-of-type(1) {
                order: -1;
              }
              &:nth-of-type(2) {
                order: 0;
              }
              &:nth-of-type(3) {
                order: -4;
              }
              &:nth-of-type(4) {
                order: -3;
              }
            }
          }
        }
        ul {
          display: flex;
          justify-content: flex-end;
          align-items: center;
          gap: 2.5rem;
        }
      }
      svg {
        z-index: 9999;
        position: relative;
        @include screen(992px) {
          width: 9rem;
        }
        path.label {
          transition: all 0.3s ease;
        }
        &:hover, &:focus {
          path.label {
            fill: var(--red);
          }
        }
        &.login-svg {
          path,
            circle {
              transition: all 0.3s ease;
            }
          &:hover, &:focus {
            path, circle {
              fill: var(--blue);
            }
          }
          @include screen(992px) {
            width: 2rem;
          }
        }
      }
    }
  }

  footer {
    margin-top: 2rem;
    .footer {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: var(--bg);
      border-radius: 0.625rem 0.625rem 0 0;
      background: #f3f3f3;
      padding: calc(var(--padding-inline) / 2) var(--padding-inline);
  
      &__nav {
        ul {
          display: flex;
          justify-content: flex-end;
          align-items: center;
          gap: 2.5rem;
        }
      }
      svg {
        position: relative;
  
        @include screen(992px) {
          width: 9rem;
        }
  
        path.label {
          transition: all 0.3s ease;
        }
  
        &:hover,
        &:focus {
          path.label {
            fill: var(--red);
          }
        }
  
        &.login-svg {
  
          path,
          circle {
            transition: all 0.3s ease;
          }
  
          &:hover,
          &:focus {
  
            path,
            circle {
              fill: var(--blue);
            }
          }
  
          @include screen(992px) {
            width: 2rem;
          }
        }
      }
    }
  }

  .home--main {
    display: flex;
    gap: 1.5rem;
    align-items: flex-start;
    justify-content: space-between;
    flex-wrap: wrap;
    &__block {
      &:last-of-type {
        @include screen(1100px) {
          order: -1 !important;
          img {
            align-self: center !important;
          }
        }
      }
      img {
        max-width: 656px; 
        border-radius: 2.5rem;
        object-fit: contain;
        object-position: center center;
        align-self: end;
      }
      display: flex;
      flex-direction: column;
      gap: 1.25rem;
      flex: 1;
      &:first-child {
        width: 100%;
        max-width: 35rem;
      }
      a {
        margin-top: 1.5rem;
      }
      &__stats {
        --columns-desktop: 3;
        --columns-mobile: 1;
        --collumn-min-size: 11rem;
        --gap: .5rem;
      
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(max(var(--collumn-min-size), calc((100% - (var(--gap) * (var(--columns-desktop) - 1))) / var(--columns-desktop))), 1fr));
        gap: var(--gap);
      
        @media (width < 768px) {
          grid-template-columns: repeat(var(--columns-mobile), minmax(0, 1fr));
        }
        .item {
          display: grid;
          grid-row: span 2;
          grid-template-rows: subgrid;
          gap: .25rem;
        }
      }
    }
  }
  .title {
    display: flex;
    justify-content: flex-start;
    gap: 2.5rem;
    align-items: center;
    h2 {
      background-color: var(--red);
      border-radius: .5rem;
      padding: .5rem 1rem;
      color: var(--white);
      width: fit-content;
    }
    p {
      max-width: 35rem;
    }
  }
  .partneers {
    --columns-desktop: 5;
    --columns-mobile: 1;
    --collumn-min-size: 13rem;
    --gap: 1.25rem;
  
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(max(var(--collumn-min-size), calc((100% - (var(--gap) * (var(--columns-desktop) - 1))) / var(--columns-desktop))), 1fr));
    gap: var(--gap);
  
    @media (width < 768px) {
      grid-template-columns: repeat(var(--columns-mobile), minmax(0, 1fr));
    }
  }
  .section--column {
    display: flex;
    flex-direction: column;
    gap: 3.5rem;
  }
  .exercises__grid {
    --columns-desktop: 2;
    --columns-mobile: 1;
    --collumn-min-size: 16rem;
    --gap: 2.5rem;
  
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(max(var(--collumn-min-size), calc((100% - (var(--gap) * (var(--columns-desktop) - 1))) / var(--columns-desktop))), 1fr));
    gap: var(--gap);
  
    @media (width < 768px) {
      grid-template-columns: repeat(var(--columns-mobile), minmax(0, 1fr));
    }
    padding-bottom: 5px;
    .item {
      display: flex;
      flex-direction: column;
      gap: 5rem;
      padding: 3rem;
      border-radius: 3rem;
      box-shadow: 0 5px 0 0 #191A23;
      background-size: cover;
      background-position: center center;
      background-repeat: no-repeat;
      > p {
        background-color: var(--blue);
        border-radius: .5rem;
        padding: .5rem 1rem;
        color: var(--white);
        width: fit-content;
      }
      div {
        display: flex;
        justify-content: start;
        align-items: center;
        gap: 1rem;
        width: fit-content;
        transition: all .3s ease;
        svg {
          transition: all .3s ease;
        }
      }
      &:hover, &:focus {
        div {
          svg {
            transform: rotate(30deg);
          }
        }
      }
    }
  }
  .news__grid {
    --columns-desktop: 4;
    --columns-mobile: 1;
    --collumn-min-size: 16rem;
    --gap: 1.25rem;
  
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(max(var(--collumn-min-size), calc((100% - (var(--gap) * (var(--columns-desktop) - 1))) / var(--columns-desktop))), 1fr));
    gap: var(--gap);
  
    @media (width < 768px) {
      grid-template-columns: repeat(var(--columns-mobile), minmax(0, 1fr));
    }
  
    padding-bottom: 5px;
    .item {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      padding: 3rem;
      border-radius: 3rem;
      box-shadow: 0 5px 0 0 #191A23;
      border: 1px solid #000;
      img {
        object-position: center center;
        object-fit: cover;
        border-radius: .625rem;
        width: 100%;
        height: 20rem;
      }
    }
  }
}

@layer components {
  .btn {
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    gap: .75rem;
    width: fit-content;
    &--blue {
      background: var(--blue);
      border: 2px solid var(--blue);
      color: var(--white);
      padding: 1.25rem 2rem;
      transition: all .3s ease;
      border-radius: 1rem;
      &:hover, &:focus {
        background: transparent;
        color: var(--blue);
      }
    }
  }
  .link {
    position: relative;
    &--underline {
      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 1px;
        width: 0;
        background-color: var(--black);
        transition: all 0.3s ease;
      }
      &--left {
        &:hover, &:focus {
          &::after {
            width: 100%;
          }
        }
      }
    }
  }
  .events {
    padding-bottom: 5px;
  }
  .events__grid {
    display: grid;
    grid-template-columns: repeat(5, minmax(0, 1fr));
    gap: 2.5rem;
    @include screen(992px) {
      grid-template-columns: repeat(2, minmax(0, 1fr));
      gap: 1.25rem;
    }
    @include screen(768px) {
      grid-template-columns: repeat(1, minmax(0, 1fr));
    }
    .item {
      display: flex;
      flex-direction: column;
      gap: 1.25rem;
      padding: 3rem 3.5rem;
      border-radius: 3rem;
      box-shadow: 0 5px 0 0 #191A23;
      border: 1px solid var(--black);
      grid-column: span 2;
      @include screen(992px) {
        grid-column: span 1;
      }
      &:is(:last-of-type, :first-of-type) {
        grid-column: span 3;
        @include screen(992px) {
          grid-column: span 1;
        }
      }
    }
  }
  .news__images {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
    img {
      width: calc(33.33333% - 1rem);
      aspect-ratio: 1;
      object-fit: cover;
      object-position: center center;
      border-radius: .625rem;
    }
  }
}